body {
  display: block;
  color: #3D3935;
  margin: 0 !important;
  font-family: "AvenirRegular", Lato, Helvetica;
}

#root {
  padding-top: 0 !important;
}

.main-container {
  background-color: #fff;
  margin-top: 0;
}

section {
  display: inherit;
}

.topsec {
  background-color: #E0DDD7 !important;
  z-index: 100;
  border-bottom: 2px solid#137044;
  margin-bottom: 5px;
}

.label {
  line-height: 34px;
}

.caption {
  font-size: .9em;
  text-transform: capitalize;
}

.dropzone {
  -ms-flex: 1 auto 0%;
      flex: 1 auto 0%;
  border-width: 0 !important;
}

.dropzone * {
  border-width: 0 !important;
}

.meta {
  background-color: #f9f9f9 !important;
  border: 1px solid #e5e5e5;
  border-radius: 12px;
  padding: 10px 10px 6px 10px;
  margin: 7px;
  min-width: 150px;
  color: #555;
  overflow: hidden !important;
  display: inline-block;
  vertical-align: top;
}

.metabeta {
  padding-left: 5px;
  font-size: .8em;
}

.preview {
  width: 100%;
  display: inherit;
  cursor: pointer;
}

div {
  margin: initial;
  line-height: 23px;
}

.meta-container {
  position: relative;
}

.meta-data {
  overflow: hidden;
  max-height: 630px;
}

.ring-loader {
  position: relative;
  top: -200px;
}

.upper {
  text-transform: uppercase;
}

h3 {
  color: #888888;
  padding: 0 0 0px 10px;
  margin: 0;
}

.title {
  font-size: 2.1em;
  padding: 0 0 10px 0px;
  color: #3D3935;
  line-height: 2.4rem;
  letter-spacing: 7px;
  font-family: Lato, Helvetica, Arial, sans-serif;
}

.container>.sectitle {
  float: right;
  text-transform: uppercase;
}

.sectitle {
  padding: 0 0 0 0;
  color: #3d3935;
  z-index: 1000;
  display: block;
}

.subtitle {
  position: relative;
  letter-spacing: 3px;
  font-size: 1em;
}

pre {
  font-size: .9em;
}

.blurb {
  padding: 15px;
  position: relative;
  letter-spacing: 2px;
  line-height: 1.5;
  font-size: .8em;
  margin-top: 20px;
  background-color: #F0F0F0;
}

.blurb>h3 {
  padding-top: 0 !important;
  margin: 0px 0 5px 0px !important;
}

.stretch {
  letter-spacing: 2px;
  font-size: .9em;
}

.metatitle {
  float: right;
  padding: 0 20px 0px 0px !important;
}

.metatitle>span {
  padding-right: 20px;
}

.error {
  background-color: rgba(255, 0, 0, .2);
  padding: 5px;
  border-radius: 5px;
}

.anger {
  background-color: #2678B2;
  color: #fff;
}

.contempt {
  background-color: #AFC8E7;
  color: #fff;
}

.disgust {
  background-color: #FD7F28;
  color: #fff;
}

.fear {
  background-color: #FDBB7D;
  color: #fff;
}

.happiness {
  background-color: #339E34;
  color: #fff;
}

.neutral {
  background-color: #9ADE8D;
  color: #fff;
}

.sadness {
  background-color: #D42A2F;
  color: #fff;
}

.surprise {
  background-color: #FD9898;
  color: #fff;
}

.infobar {
  padding: 10px;
}

.score {
  padding: 3px;
  border-radius: 5px;
  text-transform: uppercase;
}

.upper {
  text-transform: uppercase;
}

.chart {
  text-align: center;
}

.summary-chart {
  float: right;
  color: #000;
  padding: 5px;
  padding-left: 10px;
  background-color: rgba(243, 243, 243, .5);
}

.footer {
  color: #fff;
  font-family: "AvenirNext-Medium", sans-serif;
  background-color: #3d3935;
  width: auto;
  text-align: center;
  padding: 20px;
  display: block
}

.footer p {
  text-transform: uppercase;
  font-size: 0.625rem;
  line-height: 1rem;
  letter-spacing: 0.1rem;
}

.footer a {
  color: #fff;
  padding: 10px 10px;
  font-size: 0.75rem;
  text-decoration: none;
}

.footer div {
  font-size: 12px;
  font-weight: normal;
  font-size: 0.75rem;
  color: #797979;
}

li {
  padding: 0 0 10px 0;
}

li>b {
  text-transform: uppercase;
}

.saved-item {
  font-size: .9em;
}

.sb-heading--extraLarge {
  outline: none;
}

.sb-heading {
  outline: none;
  margin-top: 15px;
}

.piechart {
  text-align: center;
  margin-right: 10px;
}

li {
  padding: 0;
}

.gender {
  max-width: 300px;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.legend {
  padding: 5px 5px 0px 5px !important;
}

.swapimg {
  width: 100%;
  cursor: pointer;
}

.clickable {
  cursor: pointer;
}

.clickable:hover {
  color: #FF226B;
  text-decoration: underline;
}

.apimap {
  width: 100%;
}

.logo:hover {
  cursor: pointer;
}

.photo {
  margin-bottom: 20px;
}

.webcam-bg {
  background-image: url('./images/faceintro2.png') no-repeat !important;
  width: 200px;
  height: 200px;
  z-index: 10000;
}

.right {
  float: right;
}

.chart.meta .x.axis {
  display: none;
}

.siren {
  width: 85px;
  padding-top: 7px;
}

.siren:hover {
  cursor: pointer;
}

.css-jxiqlq {
  width: 60px;
  height: 176px;
  margin: auto;
  margin-top: 100px;
}

.css-jxiqlq {
  width: 60px;
  height: 76px !important;
  position: relative;
}


.cameraOn {
  background-color: #137044;
}

.cameraOff {
  background-color: #FF226B;
}

img.thumbnail {
  margin: 0 0 0px 10px;
  max-width: 200px;
  float: right;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.container {
  width: 600px;
  margin: 0 auto;
  -ms-flex: 0 0 10.5%;
      flex: 0 0 10.5%;
}

.after-img {
  width: 200px;
  height: auto;
}

.intro {
  padding: 20px 0 20px 0;
}

.captions {
  color: #FF226B;
}

.landmarks {
  color: #0079D4;
}

.faces {
  color: #0A6242;
}

.female {
  color: #FF226B;
}

.male {
  color: #0079D4;
}


